@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff") format("woff");
  src: url("../fonts/PTMono-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap; }

html,
body {
  overflow: hidden; }

body {
  background-color: #ffffff;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

.button {
  font-family: "Roboto Condensed";
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 5px;
  letter-spacing: 1px; }

.wrapper {
  max-width: 1920px;
  position: relative; }

.header {
  width: 100%;
  position: absolute;
  top: 0;
  padding: 0 5px; }

.header__content {
  display: flex;
  flex-direction: row;
  max-width: 1410px;
  margin: 0 auto;
  margin-top: 25px;
  padding: 10px 0; }

@media screen and (max-width: 1023px) {
  .header {
    width: 100%;
    position: absolute;
    top: 0;
    padding: 0 25px; } }

@media screen and (max-width: 767px) {
  .header__content {
    margin-top: 15px; } }

.logo {
  display: flex;
  flex-direction: row; }

.logo__img {
  width: 52px;
  height: 52px;
  margin-right: 15px; }

.logo__text {
  color: #ffffff;
  display: flex;
  flex-direction: column; }

.logo__big-text {
  font-size: 25px;
  font-weight: 600; }

.logo__small-text {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #788891; }

@media screen and (max-width: 767px) {
  .logo__text {
    display: none; } }

.contacts {
  display: flex;
  flex-direction: row;
  text-decoration: none;
  align-items: baseline;
  margin-left: auto;
  font-family: "PT Mono"; }

.contacts__email,
.contacts__phone {
  color: #ffffff;
  text-decoration: none; }

.contacts__email {
  padding-left: 32px;
  position: relative; }
  .contacts__email::before {
    position: absolute;
    content: "";
    width: 19px;
    height: 14px;
    background-image: url("../img/header/envelope.png");
    left: 0;
    top: 2px; }

.contacts__phone {
  padding-left: 32px;
  position: relative;
  margin-left: 85px; }
  .contacts__phone::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 18px;
    background-image: url("../img/header/phone-call.png");
    left: 0; }

.contacts__button {
  width: 180px;
  font-size: 14px;
  padding: 12px 5px;
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
  margin-left: 40px; }

@media screen and (max-width: 1023px) {
  .contacts {
    font-size: 0;
    color: transparent; }
  .contacts__email::before {
    width: 26px;
    height: 19px;
    background-image: url("../img/header/envelope-tablet.png"); }
  .contacts__phone {
    margin-left: 33px; }
    .contacts__phone::before {
      width: 25px;
      height: 24px;
      background-image: url("../img/header/phone-call-tablet.png"); }
  .contacts__button {
    display: none; } }

.start {
  height: 880px;
  background-color: #1B2D37;
  background-image: url("../img/start/background.png");
  padding: 0 5px;
  padding-top: 315px; }

.start__content {
  width: 1410px;
  margin: 0 auto;
  position: relative; }

.start__heading {
  font-size: 80px;
  font-weight: 600;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  letter-spacing: 6px;
  position: relative;
  z-index: 3; }

.start__text {
  width: 650px;
  font-family: "PT Mono";
  font-size: 22px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-left: 100px;
  margin-bottom: 40px;
  padding: 0;
  position: relative;
  z-index: 3; }
  .start__text::before {
    position: absolute;
    content: "";
    width: 66px;
    height: 2px;
    background-color: #ffffff;
    left: -100px;
    top: 50%; }

.start__button {
  margin-left: 100px;
  color: #ffffff;
  background-color: #00B2FF;
  border: none;
  width: 407px;
  padding: 18px 5px;
  position: relative;
  z-index: 3; }

.start__scheme {
  width: 705px;
  height: 515px;
  position: absolute;
  top: -65px;
  right: -100px;
  z-index: 2; }

.start__triangles {
  width: 611px;
  height: 457px;
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 1; }

@media screen and (max-width: 1023px) {
  .start {
    height: 685px;
    padding-top: 190px; }
  .start__heading {
    font-size: 60px;
    margin-left: 80px;
    width: 500px; }
  .start__text {
    width: 450px;
    font-size: 18px;
    margin-left: 80px;
    padding-left: 60px; }
    .start__text::before {
      width: 33px;
      left: 0px; }
  .start__button {
    margin-left: 140px;
    width: 379px; }
  .start__scheme {
    width: 465px;
    height: 340px;
    top: -80px;
    right: 430px; }
  .start__triangles {
    width: 480px;
    height: 390px;
    top: -121px;
    right: 470px; } }

@media screen and (max-width: 767px) {
  .start {
    height: 647px;
    padding-top: 285px; }
  .start__content {
    width: fit-content; }
  .start__heading {
    font-size: 40px;
    margin-left: 15px;
    width: 275px; }
  .start__text {
    width: 300px;
    font-size: 16px;
    margin-left: 15px;
    padding-left: 65px; }
    .start__text::before {
      left: 0px; }
  .start__button {
    margin-left: 15px; }
  .start__scheme {
    width: 301px;
    height: 220px;
    top: -193px;
    right: auto;
    left: 49px; }
  .start__triangles {
    width: 470px;
    height: 370px;
    top: -215px;
    right: auto;
    left: 180px; } }
